<template>
  <div class="typeAdmin">
    <!-- 内容头部 -->
    <ContTop>
      <template v-slot:input>
        <el-input v-model="input1" placeholder="名称"></el-input>
      </template>
      <template v-slot:s-btn>
        <el-button type="primary">搜索</el-button>
      </template>
      <template v-slot:add-btn>
        <el-button type="primary" @click="dialogVisible = true">新增</el-button>
        <!-- 新增对话框 -->
        <el-dialog
          :visible.sync="dialogVisible"
          width="25%"
          center>
          <el-input v-model="input2" placeholder="名称" size="medium"></el-input>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
          </span>
        </el-dialog>
      </template>
    </ContTop>
    <!-- 表格 -->
    <div class="my-bottom">
      <div class="my-table">
        <el-table
          :data="tableData.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
          style="width: 100%"
          border>
          <el-table-column
            align="center"
            label="序号"
            prop="date">
          </el-table-column>
          <el-table-column
            align="center"
            label="名称"
            prop="name">
          </el-table-column>
          <el-table-column
            align="center"
            label="活动数量"
            prop="name">
          </el-table-column>
          <el-table-column
            align="center"
            label="操作"
            width="250px"
          >
            <template slot-scope="scope">
              <el-button
                size="mini"
                @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

